@import '../utils/Colors.scss';

.testimonial-container {
    .quote {
        position: relative;
        span {
            position: absolute;
            font-size: 5rem;
            color: #f32a8f8f;
        }
    }
    height: 8rem;
    width: 95%;
    border: 10px solid transparent;
    // margin: 10px;
    border-radius: 0.4rem;
    // box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
    background-color: rgba(255, 255, 255, 0);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // transition: 0.4s ease-in-out;

    &:hover {
        // box-shadow: rgba(99, 15, 179, 0.5) 5px 5px, rgba(99, 15, 179, 0.3) 10px 10px,
        // 	rgba(99, 15, 179, 0.1) 15px 15px;
        border: none;
        // margin: none;
        width: calc(95% + 20px);
        height: calc(8rem + 20px);
        // transform: scale(1.3);
    }
    img {
        max-width: 100%;
        &:hover {
            filter: drop-shadow(12px 12px 7px rgba(255, 255, 255, 0.5));
            -webkit-filter: drop-shadow(12px 12px 7px rgba(255, 255, 255, 0.5));
            -moz-filter: drop-shadow(12px 12px 7px rgba(255, 255, 255, 0.5));
            transition: 0.5s;
        }
    }
}

@media screen and (min-width: 320px) and (max-width: 480px) {
    .testimonial-container {
        .content {
            font-size: 1.3rem;
        }
        .author {
            .name {
                font-size: large;
            }
            .designation {
                font-size: 1.4rem;
            }
        }
    }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
    .testimonial-container {
        .content {
            font-size: 1.2rem;
        }
        .author {
            .name {
                font-size: medium;
            }
            .designation {
                font-size: 1.4rem;
            }
        }
    }
}
